import {
	getSpreadUser
} from "@/api/user";
import Loading from "@/components/Loading";
export default {
	name: "PromoterList",
	components: {
		Loading
	},
	props: {},
	data: function() {
		return {
			fixedState: false,
			screen: {
				page: 1,
				limit: 15,
				grade: 0,
				keyword: "",
				sort: ""
			},
			childCount: 2,
			numberCount: 2,
			orderCount: 2,
			loaded: false,
			loading: false,
			spreadList: [],
			loadTitle: "",
			first: "",
			second: ""
		};
	},
	mounted: function() {
		this.getSpreadUsers();
	},
	onReachBottom() {
		!this.loading && this.getSpreadUsers();
	},
	watch: {
		"screen.sort": function() {
			this.screen.page = 0;
			this.loaded = false;
			this.loading = false;
			this.spreadList = [];
			this.getSpreadUsers();
		}
	},
	methods: {
		handleScroll: function() {
			// var scrollTop =
			//   document.documentElement.scrollTop || document.body.scrollTop;
			// var offsetTop = document.querySelector(".header").clientHeight;
			// if (scrollTop >= offsetTop) {
			//   this.fixedState = true;
			// } else {
			//   this.fixedState = false;
			// }
		},
		submitForm: function() {
			this.screen.page = 0;
			this.loaded = false;
			this.loading = false;
			this.spreadList = [];
			this.getSpreadUsers();
		},
		getSpreadUsers: function() {
			let that = this,
				screen = that.screen;
			if (that.loaded || that.loading) return;
			that.loading = true;
			getSpreadUser(screen).then(
				res => {
					that.loading = false;
					that.spreadList.push.apply(that.spreadList, res.data.list);
					that.loaded = res.data.list.length < that.screen.limit; //判断所有数据是否加载完成；
					that.loadTitle = that.loaded ? "人家是有底线的" : "上拉加载更多";
					that.screen.page = that.screen.page + 1;
					that.first = res.data.total;
					that.second = res.data.totalLevel;
				},
				err => {
					uni.showToast({
			title: err.msg || err.response.data.msg,
			icon: 'none',
			duration: 2000
		});
				},
				300
			);
		},
		checkGrade: function(val) {
			if (val == this.screen.grade) return;
			else {
				this.screen.page = 1;
				this.screen.grade = val;
				this.loading = false;
				this.loaded = false;
				this.spreadList = [];
				this.getSpreadUsers();
			}
		},
		sort: function(types) {
			let that = this;
			switch (types) {
				case "childCount":
					if (that.childCount == 2) {
						that.childCount = 1;
						that.orderCount = 2;
						that.numberCount = 2;
						that.screen.sort = "childCount DESC";
					} else if (that.childCount == 1) {
						that.childCount = 3;
						that.orderCount = 2;
						that.numberCount = 2;
						that.screen.sort = "childCount ASC";
					} else if (that.childCount == 3) {
						that.childCount = 2;
						that.orderCount = 2;
						that.numberCount = 2;
						that.screen.sort = "";
					}
					break;
				case "numberCount":
					if (that.numberCount == 2) {
						that.numberCount = 1;
						that.orderCount = 2;
						that.childCount = 2;
						that.screen.sort = "numberCount DESC";
					} else if (that.numberCount == 1) {
						that.numberCount = 3;
						that.orderCount = 2;
						that.childCount = 2;
						that.screen.sort = "numberCount ASC";
					} else if (that.numberCount == 3) {
						that.numberCount = 2;
						that.orderCount = 2;
						that.childCount = 2;
						that.screen.sort = "";
					}
					break;
				case "orderCount":
					if (that.orderCount == 2) {
						that.orderCount = 1;
						that.numberCount = 2;
						that.childCount = 2;
						that.screen.sort = "orderCount DESC";
					} else if (that.orderCount == 1) {
						that.orderCount = 3;
						that.numberCount = 2;
						that.childCount = 2;
						that.screen.sort = "orderCount ASC";
					} else if (that.orderCount == 3) {
						that.orderCount = 2;
						that.numberCount = 2;
						that.childCount = 2;
						that.screen.sort = "";
					}
					break;
				default:
					that.screen.sort = "";
			}
		}
	}
};
